import * as React from "react";
import NavHeader from "./navHeader";

const pageStyles = {
  fontFamily: "'Playfair Display', serif !important",
};

const Layout = ({ pageTitle, children }) => {
  return (
    <main className="container-fluid" style={pageStyles}>
      <NavHeader />
      {children}
      <div className="row">
        <div className="col text-center">&copy; Diggital</div>
      </div>
    </main>
  );
};

export default Layout;
