import * as React from "react";
import { Link } from "gatsby";
import logoImg from "../images/Lane7(TriColor).svg";

const pageStyles = {
  fontFamily: "Montserrat",
  fontWeight: 600,
};

const NavHeader = ({ pageTitle, children }) => {
  return (
    <div className="container" style={pageStyles}>
      <div class="d-flex flex-row justify-content-center  align-items-center flex-wrap bd-highlight mb-3">
        <div class="bd-highlight">
          <Link to="/" className="navbar-brand mx-0">
            <img
              alt="Lane7"
              src={logoImg}
              width={200}
              height={120}
              className="d-inline-block align-text-top"
            />
          </Link>
        </div>
        <div class="bd-highlight">
          <h1 className="text-dark" style={pageStyles}>
            Diggital
          </h1>
        </div>
      </div>
    </div>
  );
};

export default NavHeader;
